.footer {
  position: fixed;
  bottom: 0rem;
  width: 3.75rem;
  height: 1rem;
  text-align: center;
  padding: 0rem;
  margin: 0rem;
  background-color: rgb(243, 244, 247);
}
.avatar {
  display: "inline-block";
  width: 0.3rem;
  height: 0.3rem;
  border: 0.01rem solid;
  border-radius: 20rem;
}

.linkWap {
  display: flex;
  position: absolute;
  flex-direction: row;
  align-items: flex-end;
  background-color: white;
  width: 3.75rem;
  height: 0.7rem;
  bottom: 0rem;
}
.linkWap .home :first-child {
  display: none;
}

.linkWap .Myvote :first-child {
  display: none;
}

.linkWap .active :first-child {
  display: inline;
}
.linkWap .active :nth-child(2) {
  display: none;
}
.home {
  flex-grow: 1;
}

.Myvote {
  flex-grow: 1;
}

.logout {
  flex-grow: 1;
}

.login {
  flex-grow: 1;
}

.register {
  flex-grow: 1;
}
