.lists {
  margin: 0px;
  padding: 0px;
}

.lists li {
  width: 3.75rem;
  height: 0.6rem;
  padding: 0.2rem;
  margin: 0px;
}
.wrap {
  padding: 0.2rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 0.15rem;
  border-bottom: rgb(191, 191, 191) 0.01rem solid;
}
.wrap :first-child {
  color: black;
}
.wrap :nth-child(2) {
  color: rgb(191, 191, 191);
}
