.VVBody {
  display: block;
  padding: 0.2rem;
  width: 3.75rem;
  height: 6.8rem;
  background-color: rgb(243, 244, 247);
}
/* .VVBody + .footer {
  display: none;
} */
.optionsWrap {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: white;
  margin-left: -0.2rem;
  margin-right: -0.2rem;
  padding: 0.2rem;
  margin-bottom: 0.1rem;
  position: relative;
}
.checked {
  display: inline-block;
}

@keyframes CheckLine {
  0% {
    color: rgba(37, 109, 242, 0);
  }
  90% {
    color: rgba(37, 109, 242, 0);
  }
  100% {
    color: rgb(37, 109, 242);
  }
}
.checkWrap {
  position: relative;
}
.CheckOutlined {
  animation-name: CheckLine;
  animation-duration: 0.3s;

  bottom: 0rem;
  position: absolute;
}

@keyframes spinDis {
  0% {
    color: rgb(37, 109, 242);
  }
  90% {
    color: rgb(37, 109, 242);
  }
  100% {
    display: none;
  }
}
.spin {
  position: absolute;
  left: 0;
}
.spin :first-child :first-child {
  animation-name: spinDis;
  animation-duration: 0.3s;
}
.optionsWrap :nth-child(1) {
  flex-grow: 0.1;
}
.optionsWrap :nth-child(2) {
  flex-grow: 1;
}
.optionsWrap :nth-child(3) {
  flex-grow: 0.2;
  text-align: center;
}
.optionsWrap :nth-child(4) {
  flex-grow: 0.4;
  width: 0.2rem;
  text-align: right;
}
.optionsWrap :nth-child(5) {
  position: absolute;
  width: 3.2rem;
  left: 0.2rem;
  bottom: -0.08rem;
}
.time {
  padding-top: 0.2rem;
  font-size: 0.15rem;
  color: rgb(184, 190, 202);
}
