.Create {
  margin: 0rem 0.2rem 0rem 0.2rem !important;
  padding: 0rem;
  height: 100%;
}

.Create + .footer {
  display: none;
}
.Create :first-child {
  font-size: 0.17rem;
}
.dynamic-delete-button {
  position: relative;
  top: 0.04rem;
  left: 0px;
  margin: 0 0.1rem;
  color: rgb(255, 0, 0);
  font-size: 0.2rem;
  /* 大小 */
  transition: all 0.3s;
}
.dynamic-delete-button:hover {
  color: rgb(255, 0, 0);
}
.dynamic-delete-button[disabled] {
  cursor: not-allowed;
  opacity: 0.5;
}
.ant-form-item {
  margin-bottom: 0.1rem !important;
}
.titleInput {
  width: 3.75rem;
  height: 0.55rem;
  vertical-align: top;
  border-bottom: 0.01rem solid rgb(220, 220, 220) !important;
}

.descInput {
  width: 3.75rem;
  height: 0.55rem;
  border-bottom: 0.01rem solid rgb(220, 220, 220) !important;
}

.optionInput {
  border-bottom: 0.01rem solid rgb(220, 220, 220) !important;
}
.addWrap {
  padding-left: 0.1rem !important;
  padding-top: 0.05rem !important;
}
.addBut {
  position: relative;
  left: 0px;
  margin: 0 0.1rem;
  color: rgb(255, 0, 0);
  font-size: 0.2rem;
  padding: 0rem !important;
  border: 0rem !important;
}
.addBut :first-child {
  font-size: 0.2rem;
  padding: 0rem;
}
.addText {
  font-size: 0.15rem;
  padding-left: 0.1rem;
}
.deadlineInput {
  display: block;
  font-size: 0.2rem !important;
  border-bottom: 0.01rem solid rgb(220, 220, 220) !important;
  position: relative;
}
.deadlineInput :first-child {
  font-size: 0.15rem;
  position: relative;
}

.deadlineInput :nth-child(2) {
  position: absolute;
  bottom: 0rem;
  right: 0rem;
  text-align: right;
}
.deadlineInput .ant-form-item-control-input-content {
  position: relative;
  right: 0rem;
  bottom: 0rem;
  margin: 0;
}

.deadlineInput .ant-picker-borderless {
  width: 1.7rem;
  position: absolute;
  right: 0rem;
  bottom: -0.15rem;
}

.deadlineInput .ant-picker-suffix {
  display: none;
}
.anonymousInput {
  font-size: 0.15rem !important;
  border-bottom: 0.01rem solid rgb(220, 220, 220) !important;
  position: relative;
}
.anonymousInput :first-child {
  font-size: 0.15rem !important;
}
.anonymousInput :nth-child(2) {
  text-align: right;
  position: absolute;
  right: 0;
  bottom: 0rem;
}

.restrictedInput {
  position: relative;
  font-size: 0.15rem !important;
  border-bottom: 0.01rem solid rgb(220, 220, 220) !important;
}
.restrictedInput :first-child {
  font-size: 0.15rem !important;
  text-align: right;
}

.restrictedInput :nth-child(2) {
  text-align: right;
  position: absolute;
  bottom: 0rem;
  right: 0;
}

.subBut :first-child {
  font-size: 0.2rem;
}
.ant-form-item-control-input-content {
  margin: 0.13rem;
}
.anonymousInput .ant-switch {
  align-items: baseline;
}
