.signalSelect {
  width: 3.75rem;
  height: 2.375rem;
  position: relative;
  top: 0.6rem;
  left: 0rem;
  text-align: center;
}

.signalSelect button {
  position: relative;
  top: 0.3rem;
  width: 2.5rem;
  height: 0.4rem;
  background-color: rgb(37, 109, 242);
  color: white;
}
.miltSelect {
  width: 3.75rem;
  height: 1.875rem;
  position: relative;
  top: 0.6rem;
  left: 0rem;
  text-align: center;
}

.miltSelect button {
  position: relative;
  top: 0.3rem;
  width: 2.5rem;
  height: 0.4rem;
  background-color: rgb(37, 109, 242);
  color: white;
}
@media (min-width: 5.3rem) {
  .miltSelect {
    width: 3.75rem;
    height: 3.75rem;
  }
}
